import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      uid: "",
      ids: "",
      type_id: "",
      Title: "",
      tableData: [],
      value: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      visible: false,
      disabled: false,
      disabledID: false,
      rules: {
        send_name: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        title: [{ required: true, message: "必填项不能为空", trigger: "blur" }],
        content: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        coins: [{ required: true, message: "必填项不能为空", trigger: "blur" }],
        uid: [{ required: true, message: "必填项不能为空", trigger: "blur" }]
      },
      form: {
        send_name: "",
        title: "",
        content: "",
        coins: "",
        uid: []
      },
      title: "记录"
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      mail_type: 2
    });
  },
  methods: {
    //写邮件
    writeEmail() {
      this.editForm("记录", true, false,false, {});
    },

    //按条件搜索
    search() {

      if (this.ids === "") {
        this.ids = 0;
      } else if (this.type_id === "") {
        this.type_id = 0;
      } else if (this.uid === "") {
        this.uid = 0;
      }
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 2,
        title: this.Title,
        uid: +this.uid,
        ids: +this.ids,
        type_id: +this.type_id
      });
      this.ids = "";
      this.type_id = "";
      this.uid = "";
      this.Title = "";
    },

    //表格编辑
    handleEdit(row) {
      // console.log(row);
      row.mail_id = row.id;
      this.editForm("编辑", true, false,true, DeepData(row));
    },

    //表格详情
    detail(row) {
      // console.log(row);
      this.editForm("邮件详情", true, true, true, DeepData(row));
    },

    //表格发送邮件
    async send(row) {
      // console.log(row);

      let resData = {
        mail_type: 2,
        send_status: 2,
        mail_id: row.id
      };
      let { data } = await this.$http.HallFunConfig.patchEmail(resData);
      // console.log(data);
      if (data.code === 1 && data.msg === "ok") {
        this.initdata({
          page: this.currentPage,
          limit: this.limit,
          mail_type: 2
        });
      }
    },

    //页容量改变
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 2
      });
    },

    //页码改变
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 2
      });
    },
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === "记录") {
            console.log(this.form);
            let resData = DeepData(this.form);
            resData.mail_type = 2;
            let { data } = await this.$http.HallFunConfig.PostEmail(resData);
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                mail_type: 2
              });
            }
          } else if (type === "编辑") {
            // console.log(this.form);
            // this.form.mail_type = 2;

            let res = {
              send_name: this.form.send_name,
              title: this.form.title,
              content: this.form.content,
              coins: this.form.coins,
              uid: this.form.uid,
              mail_type: 2,
              mail_id: this.form.mail_id
            };
            let { data } = await this.$http.HallFunConfig.PutEmail(res);
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                mail_type: 2
              });
            }
          } else if (type === "邮件详情") {
          }
          this.editForm("记录", false, false,false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, disabled,disabledID, form) {
      this.title = title;
      this.visible = visible;
      this.disabled = disabled;
      this.disabledID = disabledID;
      this.form = form;
    },
    formateData(res) {
      res.forEach(item => {
        // item.send_status = item.send_status === 1 ? "待发送" : "已发送";
        if (item.send_status === 1) {
          item.send_status = "待发送";
          this.sendShow = true;
        } else if (item.send_status === 2) {
          item.send_status = item.read_status === 1 ? "未读" : "已读";
        }
      });
      return res;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetEmail(params);
      let fres = this.formateData(data.data);
      this.tableData = fres;
      this.total = data.total;
      if (this.total === 0) {
        this.$message({
          type: "warning",
          message: "没有找到合适的数据!"
        });
      }
      // console.log(data);
    }
  }
};



